import React from 'react';

export default function Errors({ errors }) {
  const errorsKeys = Object.keys(errors);
  if (!errorsKeys.length) return null;

  const onClick = (e) => {
    e.preventDefault();
    alert(JSON.stringify(errors[e.currentTarget.dataset.id]));
  };

  return (
    <>
      <h3 className="mt-5">Requests aborted: </h3>
      <ol>
        {errorsKeys.map(key => (
          <li key={key}>
            <a href="#" data-id={key} onClick={onClick}>
              {key}
            </a>
          </li>
        ))}
      </ol>
    </>
  );
}
