export function pricesCompare(array, coef) {
  const arr = [...array];
  let counter = 0;

  for (let i = 0; i < Math.ceil(arr.length / 2); i++) {
    const index = arr.findIndex(([_, price]) => price > arr[i][1] * coef);

    if (index > 0) {
      counter++;
      arr.splice(index, 1);
    }
  }

  return counter;
}
