import { dateFormat } from './';

export function findInterval(prices = [], startDate, endDate) {
  // performance 26.5 ms for 1928 items
  const start = dateFormat(startDate);
  const end = dateFormat(endDate);
  let startIndex = NaN;
  let endIndex = NaN;

  prices.forEach(([date], index) => {
    const currDate = dateFormat(date);
    if (Number.isNaN(startIndex) && (currDate >= start || index === prices.length - 2)) startIndex = index;
    if (Number.isNaN(endIndex) && (currDate >= end || index === prices.length - 1)) endIndex = index;
  });

  return prices.slice(startIndex, endIndex);
}
