import { csgoId } from '../constants/cs-go';
import { thirdPartyKey } from '../constants/api-keys';

const marketPrices = 'https://steamcommunity.com/market/pricehistory/';
// const marketPrices = 'https://google.com/';
const steamApisDomain = 'https://api.steamapis.com/market';

const callApi = async (endpoint, options = {}) => {
  try {
    const response = await fetch(endpoint, {
      method: 'GET',
      mode: 'no-cors',
      ...options
    });

    const contentType = response.headers.get('content-type');
    if (response.status === 200 && contentType && contentType.indexOf('application/json') !== -1) {
      return await response.json();
    }
    return { errors: 'Unexpected content type' };
  } catch (err) {
    // eslint-disable-next-line no-console
    console.warn('error from API request', { err });
    return { errors: 'Can\'t format into JSON' };
  }
};

// ?country=DE&currency=3&appid=&market_hash_name=
// {
//   "country" = "US",    # two letter ISO country code
//   "currency"= 1,       # 1 is USD, 3 is EUR, not sure what others are
//   "appid"   = 753,     # this is the application id.  753 is for steam cards
//   "market_hash_name="322330-Shadows and Hexes" # this is the name of the item in the steam market.
// }
export const medianPrices = (itemName, gameId = csgoId) => (
  callApi(`${marketPrices}?appid=${gameId}&market_hash_name=${itemName}`)
);

export const csGoItemsList = () => (
  callApi(`${steamApisDomain}/items/${csgoId}?format=compact&api_key=${thirdPartyKey}`)
);
