/* eslint-disable */
import React, { useState } from 'react';
import { clear } from 'idb-keyval';
import { DatePicker, GlitchButton } from '../../components/UI';

export default function Filter({
  startDate,
  setStartDate,
  endDate,
  setEndDate,
  fetchFewWeps,
  stopRequests,
  times,
  setTimes,
  multiply,
  setMultiply,
  lastFetched,
  getSortedItems,
  coef,
  setCoef,
  setDbKeys,
  sortByPrice,
  sortByEntries
}) {
  const [show, setShow] = useState(true);

  const toggleFilter = e => {
    e.preventDefault();
    setShow(prev => !prev);
  };

  const clearDb = () => {
    setDbKeys(0);
    clear();
  };

  return (
    <div className={`filter${show ? '' : ' hide'}`}>
      <span className="last-fetched">
        <a className="hide-chevron" href="#" onClick={toggleFilter}>
          <i className={`mdi mdi-chevron-left${show ? ' mdi-rotate-180' : ''}`}/>
        </a>
        Last time fetched: {lastFetched || 'never'}
      </span>
      <div className="d-flex justify-content-between input-container">
        Times: <input placeholder="times" value={times} onChange={e => setTimes(+e.target.value)} />
      </div>
      <div className="d-flex justify-content-between input-container">
        Multiply: <input placeholder="multiply" value={multiply} onChange={e => setMultiply(+e.target.value)} />
      </div>
      <div className="d-flex mt-1">
        <GlitchButton onClick={fetchFewWeps}> fetch </GlitchButton>
        <GlitchButton onClick={stopRequests}> stop </GlitchButton>
      </div>
      <div className="d-flex justify-content-between input-container mt-3">
        Coef: <input placeholder="coef" value={coef} onChange={e => setCoef(e.target.value)} />
      </div>
      <div className="d-flex justify-content-between input-container">
        Start: <div><DatePicker value={startDate} onChange={setStartDate} /></div>
      </div>
      <div className="d-flex justify-content-between input-container">
        End: <div><DatePicker value={endDate} onChange={setEndDate} /></div>
      </div>
      <div className="d-flex flex-column mt-1">
        <GlitchButton onClick={getSortedItems}> apply </GlitchButton>
        <GlitchButton onClick={sortByPrice}> sort by price </GlitchButton>
        <GlitchButton onClick={sortByEntries}> sort by entries </GlitchButton>
        <GlitchButton onClick={clearDb}> clear db </GlitchButton>
      </div>
    </div>
  );
}
