const stopRequests = () => {
  breaks = !breaks;
};

function Main() {
  const [state, setState] = useState([]);
  const [errors, setErrors] = useState({});

  const [startDate, setStartDate] = useState(new Date().setMonth(new Date().getMonth() - 1));
  const [endDate, setEndDate] = useState(new Date());
  const [coef, setCoef] = useState(1.15);
  const [multiply, setMultiply] = useState(100);
  const [times, setTimes] = useState(1);

  const [loading, setLoading] = useState(false);
  const [filtering, setFiltering] = useState(false);
  const [lastFetched, setLastFetched] = useState('');
  const [fetchedCount, setFetchedCount] = useState(0);
  const [dbKeys, setDbKeys] = useState(0);

  useEffect(() => {
    setLastFetched(localStorage.getItem('last_fetched'));
  }, []);

  useEffect(() => {
    if (!loading) keys().then(keys => setDbKeys(keys.length));
  }, [loading]);

  const updateLastFetchDate = () => {
    const date = moment(new Date()).format('DD.MM.YYYY HH:mm');
    localStorage.setItem('last_fetched', date);
    setLastFetched(date);
  };

  const fetchStateInit = () => {
    setLoading(true);
    setFetchedCount(0);
    setState([]);
    updateLastFetchDate();
  };

  const fetchFewWeps = async () => {
    fetchStateInit();

    for (let i = 0; i !== times; i++) {
      if (breaks) {
        setLoading(false);
        breaks = false
        break;
      }
      const requests = [];
      const results = {};

      for (let j = i * multiply; j < (i + 1) * multiply; j++) {
        requests.push(medianPrices(items[j]).then(res => {
          setFetchedCount(prev => prev + 1);
          if (res && res.prices) {
            const index = res.prices.findIndex(([date]) => dateFormat(date) > dateFormat('Jan 01 2020'));
            results[items[j]] = res.prices.slice(index);
          } else {
            setErrors(prev => ({ ...prev, [items[j]]: res }));
          }
        }));
      }

      // eslint-disable-next-line no-await-in-loop
      await Promise.all(requests);
      if (i === times - 1) setLoading(false);
      const res = Object.entries(results);
      setMany(res).catch(console.error);
    }
  };

  const getSortedItems = async () => {
    try {
      setFiltering(true);
      setState([]);
      const dbEntries = await entries();
      const result = [];
      dbEntries.forEach(([key, value]) => {
        const prices = findInterval(value, startDate, endDate);
        const pricesSorted = [...prices].sort(([, price1], [, price2]) => price1 - price2);
        const counter = pricesCompare(pricesSorted, +coef);
        if (counter > 0) result.push({ id: key, prices, counter });
      });

      setState(result.sort((a, b) => b.counter - a.counter));
    } catch (err) {
      console.error(err);
    } finally {
      setFiltering(false);
    }
  };

  const sortByPrice = () => {
    const sortedByPrice = [...state].sort((a, b) => {
      const [, aPrice] = a.prices[a.prices.length - 1]
      const [, bPrice] = b.prices[b.prices.length - 1]
      return aPrice - bPrice;
    });
    setState(sortedByPrice);
  };

  const sortByEntries = () => {
    const sortedByEntries = [...state].sort((a, b) => b.counter - a.counter);
    setState(sortedByEntries);
  }

  return (
    <div className="container-fluid">
      <h6>items stored: {dbKeys}</h6>
      <Filter
        setMultiply={setMultiply}
        multiply={multiply}
        setTimes={setTimes}
        times={times}
        setCoef={setCoef}
        coef={coef}
        startDate={startDate}
        setStartDate={setStartDate}
        endDate={endDate}
        setEndDate={setEndDate}
        fetchFewWeps={fetchFewWeps}
        lastFetched={lastFetched}
        stopRequests={stopRequests}
        getSortedItems={getSortedItems}
        setDbKeys={setDbKeys}
        sortByPrice={sortByPrice}
        sortByEntries={sortByEntries}
      />
      <div className="fetch-result">
        {filtering && <GlitchText sub="applying filters..." title="" />}
        {loading && <GlitchText sub={`already fetched: ${fetchedCount}`} title="FETCHING..." />}
        <Errors errors={errors} />
        <Results results={state} />
      </div>
    </div>
  );
}

export default Main;
