import React, { useState } from 'react';
import ItemChart from '../../../components/ItemChart';

const urlBase = 'https://steamcommunity.com/market/listings/730';

function ResultsItem({ item, index }) {
  const [show, setShow] = useState(false);
  const [line, setLine] = useState(false);

  const toggleGraph = e => {
    e.preventDefault();
    setShow(prev => !prev);
  };

  const onContextMenu = e => {
    e.preventDefault();
    setLine(prev => !prev);
  };

  return (
    <>
      <tr>
        <td>{index + 1}</td>
        <td>
          <a
            href="#"
            className={`allcaps ${show ? 'text-primary' : ''} ${line ? 'text-danger line-through' : ''}`}
            data-id={item.id}
            onClick={toggleGraph}
            onContextMenu={onContextMenu}
          >
            {item.id}
          </a>
        </td>
        <td className="text-center">
          <span className="text-success">{item.counter}</span>
        </td>
        <td className="text-center">
          <a rel="noreferrer" target="_blank" href={`${urlBase}/${encodeURIComponent(item.id)}`}>
            <i className="mdi mdi-open-in-new" />
          </a>
        </td>
      </tr>
      {show && (
        <tr>
          <td colSpan={4}>
            <ItemChart data={item.prices}/>
          </td>
        </tr>
      )}
    </>
  );
}

export default React.memo(ResultsItem);
