import React from 'react';
import Chart from 'react-apexcharts';

export default function ItemChart({ data = [] }) {
  if (!Array.isArray(data)) return null;

  const prices = [];
  const dates = [];

  data.forEach(([date, price]) => {
    prices.push(price);
    dates.push(date);
  });

  return (
    <Chart
      options={{
        colors: ['#2adc00'], //  
        chart: { id: 'apexchart-example' },
        stroke: {
          show: true,
          width: 1,
        },
        tooltip: {
          enabled: true,
          followCursor: true,
          theme: 'dark',
          style: {
            fontSize: '12px',
          },
          x: {
            show: false,
            format: 'dd MMM yyyy',
          },
        },
        yaxis: {
          labels: {
            style: {
              colors: '#ffffff',
            }
          }
        },
        xaxis: {
          categories: dates,
          labels: {
            format: 'MMMM dd yyyy',
            style: {
              colors: '#ffffff',
              fontSize: '12px',
              fontFamily: 'Helvetica, Arial, sans-serif',
              fontWeight: 400,
              cssClass: 'apexcharts-xaxis-label',
            },
          },
        },
        markers: {
          size: 0,
          strokeColors: ['#fff'],
          colors: ['#2adc00'] //  
        },
      }}
      series={[{ name: '.', data: prices }]}
      type="line"
      width={1000}
      height={320}
    />
  );
}
